.shorts{
    width: 205px;
    margin-bottom: 30px;
    gap: .5rem;
}

.shorts__thumbnail{
    height: 330px;
    width: 100%;
    border-radius: 7px;
    box-shadow: 1px 1px 5px rgb(70, 70, 70);
    background-position: center;
    background-size: cover;
}

.shorts__info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}

.shorts__text{
    margin-left: 15px;
}

.shorts__text h4{
    font-size: 14px;
    margin-bottom: 5px;
}

.shorts__text > p{
    font-size: 12px;
    color: grey;
    font-weight: 100;
}

.shorts__avatar{
    height: 30px;
    width: 30px;
}

@media screen and (max-width: 600px) {
    .shorts{
        width: 170px;
    }
    .shorts__thumbnail{
        height: 220px;
        width: 100%;
    }
    .shorts__info{
        width: 100%;
        padding: 0;
    }
}