

.sidebar{
    flex: 0.3;
    transition: all 500ms ease-in;
    height: 600px;
    position: sticky;
    top: 0;
    overflow: hidden;
    padding: 10px 5px 0 10px;
}

.sidebar:hover{
    overflow: scroll;
}


.sidebar > hr{
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 100;
}

.sidebar h3{
    font-weight: 100;
    padding-left: 8px;
}

@media screen and (max-width: 600px) {
    .sidebar{
        display: none;
    }
}
