.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: #fff;
}

.header__logo{
    display: flex;
    align-items: center;
}

.header__left{
    display: flex;
    align-items: center;
}

.youtube__logo{
    font-size: 25px;
    color: red;
    margin-left: 20px;
}

.header__input{
    display: flex;
    align-items: center;
    width: 50%;
    
    height: 30%;
    
}

.input__button{
    width: 50px !important;
    font-size: 14px;
    color: grey;
}

.header__input > input{
    flex: 1;
    padding: 10px;
    border-radius: 50px;
    width: 100%;
    height: 100%; 
    border: 1px solid lightgray;
}


.header__icons{
    display: flex;
    align-items: center;
    gap: 2rem;
}

@media screen and (max-width: 600px) {
    .header__input{
        display: none;
}
}