.recommended{
    padding: 20px 20px;
    padding-bottom: 0;
    width: 100%;
}

.recommendedVideos__videos{
    display: flex;
    flex-wrap: wrap;
    gap: .8rem;
}

.shorts__grid{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
}

.youtube__button{
    padding: 10px 10px;
}

.youtube__button button{
    border: none;
    background: rgb(235, 235, 235);
    border-radius: 10px;
    padding: 8px;
    font-weight: 600;
}

@media screen and (max-width: 600px) {
    .youtube__button{
        display: none;
    }

    .shorts__grid{
        grid-template-columns: repeat(2, 1fr);
        gap: .5rem;
    }
}