.sidebarrows{
    display: flex;
    align-items: center;
    padding: 13px 20px;
    transition: all 500ms ease;
}

.sidebarrows:hover{
    background: rgb(239, 239, 239);
    cursor: pointer;
}

.sidebarrow__icon{
    font-size: 20px !important;
    transition: all 500ms ease;
}

.sidebarRow__title{
     flex: 1;
     margin-left: 25px;
     font-size: 15px;
     font-weight: 100;
     transition: all 500ms ease;
}

.sidebarrows:hover > .sidebarrow__icon{
    color: red;
    translate: 5px;
}

.sidebarrows.selected {
    background: rgb(239, 239, 239);
}

.sidebarrows.selected > .sidebarrow__icon {
    color: red;
}