*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
}

body{
    font-family: sans-serif;
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-thumb{
    background: rgb(172, 172, 172);
    border-radius: 10px;
}

.app__page{
    display: grid;
    grid-template-columns: 15% 83%;
    gap: 1.7rem;
}

@media screen and (max-width: 600px) {
    .app__page{
        grid-template-columns: 1fr;
        padding: 0;
    }

    hr{
        display: none;
    }
}