.videoCard{
    width: 350px;
    margin-bottom: 30px;
    gap: .5rem;
  
}

.videocard__thumbnail{
    height: 185px;
    width: 100%;
    border-radius: 7px;
    box-shadow: 1px 1px 5px rgb(159, 159, 159);
    background-position: center;
    background-size: cover;
}

.videoCard__info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}

.videoCard__text{
    margin-left: 15px;
}

.videoCard__text h4{
    font-size: 14px;
    margin-bottom: 5px;
}

.videoCard__text > p{
    font-size: 12px;
    color: grey;
    font-weight: 100;
}

.videoCard__avatar{
    height: 30px;
    width: 30px; 
}

@media screen and (max-width: 600px) {
    .videoCard{
        width: 100%;
        gap: 0;
    }
    .videocard__thumbnail{
        height: 200px;
        border-radius: 0;
    }

}